import React from 'react';
import MAP from './componentInfo.tpl';


export default function Components(props) {
  const name = props.params.name;
  const elem = MAP[name] && MAP[name].demo ?
    React.createElement(MAP[name].demo, null) :
    <div>{`没有 ${name} 组件`}</div>;

  return (
    <div>{elem}</div>
  );
}
